import { BaseValueObject, StringOrNull } from '../Base';

export const MAX_REPORT_TYPE_CODE_LENGTH = 20;
export const MAX_FILE_NAME_LENGTH = 255;

export abstract class NonEmptyString<
  T extends string,
  E extends string | StringOrNull,
> extends BaseValueObject<T, E> {
  protected validate(value: E): E {
    if (value !== null) {
      const trimmed = value.trim();
      if (trimmed.length === 0) {
        throw new Error(`${this.constructor.name} must be not empty`);
      }
    }
    return value;
  }
}
